.pagination {
	padding-top:60px;
	text-align:center;
	width:530px;
	margin:0 auto;

	&, a {color:#2b5275;}
	&:after{content:""; display:block; clear:both;}

	.btn {
		float:left;
		border:1px solid #ebebeb;
		margin-left:-1px;
		padding:15px;

		&:first-child {margin-left:0;}
		&.hover{background:#ebebeb;}
	}

	/* 확장 */
	&.cobalt {
		> .btn.active {
			background:#2b5275;
			> a {color:#fff;}
		}
	}

	&.green {
		> .btn.active {
			background:#0f0;
			> a {color:#fff}
		}
	}
}