.btn-link {
	display:inline-block;
	padding:20px 40px;
	border:0;
	font-weight:bold;
	background-color:#fff;
	color:$cobalt;

	&.cobalt {
		color:#fff;
		background:$cobalt;
	}

	&.gray {
		color:#fff;
		background:$gray-0;
	}

	&.small {
		padding:8px 10px;
		font-size:12px;
	}

	&.full {
		display:inline-block;
		box-sizing:border-box; padding:0; width:100%;
		padding:18px 0;
		margin-top:10px;
		text-align:center;
		font-size:14px;
		font-weight:700;
		color:#fff;
		background:$cobalt;
	}

	&.line {border: 2px solid $cobalt; color:$cobalt; background:#fff;}
}

.btn-filter {
	margin:0;
	border:0;
	padding:8px 10px;

	cursor:pointer;
	font-size:14px;
	background:#fff;

	> .label{
		font-weight:bold;
		color:$cobalt;
	}

	> .total-num {
		margin-left:5px;
		color:#999;
	}


	/* position */
	&.right {
		position:absolute;
		right:0;
	}
}

.btn-layer {
	@include reset-a;
	text-decoration:underline;
}

.btn-delete {
	position:absolute;
	top:0;
	right:0;
	border:0;
	padding:0;
	margin:0;
	background-color:#fff;
	font-size:16px;
	cursor:pointer;
}


.btn-tab {
	@include reset-a;

	float:left;
	font-size:18px;
	font-weight:bold;
	margin-left:20px;
	padding-bottom:10px;
	letter-spacting:-1px;

	&:first-child {margin-left:0}
	&.active {
		border-bottom:2px solid $cobalt;
		a {color:$cobalt;}
	}
	&.over {
		border-bottom:2px solid #999;
	}
}