.customer-contents {padding:0 240px;}
.customer-faq {
	.list {
		> .question {
			height:50px;
			box-sizing:border-box;
			padding:19px 20px;
			background-color:#fff;
		}

		> .answer {
			padding:0 30px 0px 30px;
			line-height:20px;
			color:$gray-1;

			&.open {
				padding-bottom:20px;
			}
		}

		&.style-tab {
			> .question {background-color:$gray-0;}
			> .answer {padding-top:10px;}
		}
	}
}

.customer-info {
	text-align:center;
	letter-spacing:-1px;

	.tit {
		margin:10px 0 30px;
		font-size:23px;
		font-weight:bold;
		color:$cobalt;
	}	
}