.contents-maxwidth {
	> .location-map{
		overflow:hidden;
		height:520px;
		border:1px solid $gray-0;
		padding-left:301px;
		background:$gray-0;
	}
}

.location-search {
	position:absolute;
	width:300px;
	border:1px solid $gray-0;
	font-size:12px;
	background-color:#fff;

	.search-result {
		overflow-x:auto;

		> .search-list {
			border-top:1px solid $gray-0;
			&:first-child {border-top:0}

			> a {
				display:block;
				padding:20px 24px;
				color:$gray-1;
			}

			.tit {
				font-size:14px;
				font-weight:bold;
				color:$cobalt;
				letter-spacing:-1px;
			}

			.address {
				overflow:hidden;
				display:inline-block;
				max-height:34px;
				margin-top:8px;
				line-height:18px;
			}

			.phonenum {
				display:inline-block;
				margin-top:5px;
			}
		}
	}

	&.normal {
		top:0;
		left:0;
		> .search-result {
			height:446px;
		}
	}
	
	&.full {
		top:40px;
		left:20px;
		> .search-result {
			min-height:120px;
		}
	}
}

.location-map {}