.section-cart {
	position:fixed;
	top:0;
	right:0;
	bottom:0;	
	width:100%;
	max-width:330px;
	font-size:12px;
	background:#fff;
	z-index:1000;

	.cart-order_list {
		.order-list {
			position:relative;
			padding:30px 0 30px 30px;
			border-top:1px solid $gray-0;
			&:first-child {border-top:0}
			&:after {content:""; display:block; clear:both;}

			.btn-delete {top:30px; right:20px;}
			.image-wrap {float:left; overflow:hidden; width:60px; height:74px; background:$cobalt;}
			.order-info {
				float:left;
				width:182px;
				padding-left:20px;

				.tit {
					overflow:hidden;
					display:inline-block;
					min-width:100%;
					max-height:32px;
					line-height:16px;
					font-weight:bold;
					color:$cobalt;
				}

				.option, .qty {
					display:inline-block;
					min-width:100%;
					margin-top:8px;
					color:$gray-1;
				}

				.price {
					display:inline-block;
					min-width:100%;
					margin-top:8px;
					font-weight:bold;
					letter-spacing:-1px;
					color:$cobalt;
				}
			}
		}
	}

	.cart-order_price {
		padding:30px;
		border-top:1px solid $gray-0;
		color:$gray-1;

		.order-price, .delivery-price {
			display:inline-block;
			min-width:100%;

			> strong {
				display:inline-block;
				text-align:right;
				font-size:16px;
				font-weight:normal;
				min-width:80%;
				letter-spacing:-1px;
				color:$cobalt;
			}
		}
		> span {
			margin-top:30px;
			&:first-child {margin-top:0;}
		}
	}

	.cart-order_buy {
		padding:30px;
		border-top:1px solid $gray-0;
		color:$cobalt;

		.total-price {
			> strong {
				display:inline-block;
				min-width:60%;
				margin-bottom:30px;
				text-align:right;
				font-size:24px;
			}
		}
	}
}