.product-recent {
	width:63px;
	font-size:11px;
	position:absolute;
	right:0;
	z-index:11;

	.recent-list {
		min-height:70px;
		margin-top:15px;
		border:1px solid #ebebeb;
		background-color:#ebebeb;
	}
}

.product-thumb {
	position:absolute;
	z-index:10;

	.thumb-list{
		min-height:70px;
		margin-top:15px;
		border:1px solid #ebebeb;
		background-color:#ebebeb;

		&:first-child {margin-top:0;}
	}	
}

.product-detail_wrap {
	padding:0 100px;
	&:after {content:""; display:block; clear:both;}

	.img-detail {
		float:left;
		position:relative;
		width:50%;
		padding-right:30px;
		box-sizing:border-box;
		text-align:center;
		background-color:$cobalt;
		min-height:600px;

		.product-ico {
			position:absolute;
			right:30px;
			top:-20px;
			font-size:60px;
			color:$cobalt;
		}
	}

	.info-wrap {
		float:left;
		width:50%;
		padding-left:30px;
		letter-spacing:-1px;
		color:#999;
		box-sizing:border-box;

		.price-wrap {
			font-size:14px;

			> .price-sale {text-decoration:line-through;}
			> .price {
				font-weight:bold;
				color:#2b5275;
				> strong {
					font-size:30px;
				}
			}
		}

		.option-wrap {
			margin-top:30px;
			> .input-select {
				margin-top:10px;
				&:first-child {margin-top:0;}
			}
		}
		.status-wrap {
			.btn-link {width:80%; text-align:center; margin-bottom:30px;}
			.selected-opt {
				&:first-child {border-top:0;}
				border-top:1px solid #ebebeb;
				padding: 10px 0;

				.opt {width:50%;}
				.quantity {
					position:relative;
					top:0;
					> label > input {width:30px; height:30px; text-align:center;}
					> button {
						position:absolute; right:0; width:15px; height:15px; z-index:99;
						&.plus {top:0;}
						&.minus {bottom:0;}
					}
				}
				.total-price {
					color:#2b5275;
					> strong {font-weight:bold; font-size:20px;}
				}

				dt, dd {display:inline-block;}
			}
		}
		.sns-wrap {
			margin-top:30px;
			font-size:30px;
		}
	}
}


.product-detail_view {
	margin-top:60px;
	padding:0 100px;

	.detail-tab {
		width:333px;
		margin:0 auto;
		&:after {content:""; display:block; clear:both;}
	}
	.detail-info {
		padding:30px 0;
	}
}



.review-read {
	.read-list{
		margin-top:46px;
		&:first-child {margin-top:0;}

		.user-info {
			font-color:$gray-1;
			font-size:12px;
		}

		.read-subject {
			margin:27px 0 17px;
			font-size:15px;
			font-weight:bold;
			color:$cobalt;
		}

		.read-comment {
			position:relative;
			line-height:20px;
			&.append-img {min-height:80px; padding-left:90px;}

			> img {
				position:absolute;
				width:80px;
				height:80px;
				margin-left:-90px;
			}
		}
	}
}



.review-write {
	margin-top:80px;

	fieldset {
		&:after {content:""; display:block; clear:both;}
		> legend {
			text-indent:-9999px;
			position:absolute;
			width:0;
			height:0;
			overflow:hidden;
			visibility:hidden;
		}
	}

	.tit {font-weight:bold; font-size:16px; color:$cobalt;}
	.like {margin:30px 0;}
	.write-subject, .write-comment {
		display:block;
		margin:15px 0;
		padding:10px 15px;
		background-color:$gray-0;

		& > input[type="text"], & > textarea {
			width:100%;
			border:0;
			margin:0;
			padding:0;
		}

		& > textarea {min-height:200px;}
	}

	.input-file_image {float:left;}
	.input-submit {float:right;}
}