.slider-range {
	position:relative;
	background-color:#dbdbdb;

	.range-handler{
		position:absolute;
		top:-10px;
		left:0;
		margin-left:-15px;
		font-size:30px;
		color:#999;
		z-index:10;
	}

	.range-track{
		width:40%;
		position:absolute;
		height:10px;
		background:red;
	}
}