.contents-full{
	@include contents-article;
	background:$gray-0;

	> .contents-maxwidth {
		position:relative;
		max-width:$max-width;
		margin:0 auto;
	}
}

.contents-normal{
	@include contents-article;
	max-width:$max-width;
}

.keyvisual-gallery {
	position:relative;
	overflow:hidden;

	.imgwrap {max-height:700px;}
	.info {
		position:absolute;
		bottom:25%;
		width:100%;

		.tit {@include info-setup; font-size:68px; color:#fff;}
		.subtit {@include info-setup; margin-top:18px; color:#fff;}
		.link {@include info-setup; margin-top:40px;}
	}
}

.product-item {
	.item-imgwrap {
		overflow:hidden;
		position:relative;
		max-height:310px;
		background:$cobalt;

		> img { width:100%; }

		.item-label-ico {
			position:absolute;
			font-size:60px;
			color:#2b2b2b;

			&.left {left:0;}
			&.right {right:0;}
			&.bottom {bottom:0;}
		}
	}

	.item-info {
		margin-top:10px;
		color:$gray-font;

		.item-title {
			overflow:hidden;
			display:block;
			max-height:42px;
			line-height:20px;
			letter-spacing:-1px;
			font-size:12px;
		}

		.item-price {
			display:block;
			position:relative;
			margin-top:8px;
			font-size:16px;

			.item-price-sale {
				color:$gray-1;
				text-decoration:line-through;
			}

			.item-price-per {
				position:absolute;
				right:10px;
			}
		}
	}
}

.product-box {
	.image-wrap {
		background:$cobalt;
		> img {
			width:100%;
		}
	}

	.tit {
		overflow:hidden;
		max-height:40px;
		margin-top:15px;
		line-height:20px;
		font-weight:bold;
		color:$cobalt;
		&.center {text-align:center;}
	}

	.subtit {
		overflow:hidden;
		max-height:32px;
		margin-top:10px;
		line-height:16px;
		font-size:12px;
		color:$gray-1;
	}
}

.product-banner {
	overflow:hidden;
	max-height:120px;
	background:$cobalt;

	img {
		width:100%;
	}
}


.bread-crumb {
	padding:50px 0;
	color:$cobalt;
	
	.tit {
		font-weight:bold;
		font-size:50px;
		text-align:center;
		color:$cobalt;
	}

	.crumb {
		text-align:center;
		> .current{color:$gray-1;}
	}
}


.filter-wrap_category {
	position:relative;
}


.dim {
	display:none;
	position:absolute;
	overflow:hidden;
	text-indent:-9999px;

	&.active{
		display:block;
		position:fixed;
		top:0;
		left:0;
		right:0;
		bottom:0;
		background:#000;
		opacity:0.8;
		filter:alpha(opacity=0.8);
		z-index:990;
	}
}