.input-price {
	padding:0 5px;
	height:25px;
	border:1px solid #ebebeb;
	color:#999;

	&.small {width:100px;}
	&.medium {width:200px;}
	&.large {width:300px;}
}

.input-radio {
	@include input-filter;

	input[type="radio"] {
		display:none;
		position:absolute;
		opacity:0;
		filter:alpha(opacity=0);
	}
}

.input-checkbox {
	@include input-filter;

	input[type="checkbox"] {
		display:none;
		position:absolute;
		opacity:0;
		filter:alpha(opacity=0);
	}

	&.size {
		width:45px;
		text-align:center;
		border:1px solid #999;
		padding:10px 0;
		
		&.over {background-color:#eee}
		&.active {
			background-color:#2b5275;
			.label {color:#fff;}
		}
	}
}


.input-file_image {}
.input-submit {
	width:160px;
	height:50px;
	border:0;
	padding:0;
	margin:0;
	color:#fff;
	background-color:$cobalt;
	cursor:pointer;
}

.input-select {
	position:relative;	
	cursor:pointer;
	
	select {
		display:none;
		overflow:hidden;
		text-ident:-9999px;
		opacity:0;
		filter:alpha(opacity=0);
		position:absolute;
	}

	.select-head {
		display:block;
		padding:16px;
		background-color:#ebebeb;

		> .label {display:inline-block; width:42px; text-align:left; margin-right:20px; font-size:12px;}
		> .current-opt {color:#333;}
	}

	.select-body {
		display:none;
		overflow-y:auto;
		position:absolute;
		left:-9999px;
		width:100%;
		box-sizing:border-box;
		max-height:200px;
		border:1px solid #ebebeb;
		background-color:#fff;
		z-index:90;

		> .list {
			padding:15px 16px;
		}
	}

	&.active {
		.select-body {display:block;left:0;}
	}
}


.select-box {
	position:relative;
	width:200px;
	min-height:36px;
	box-sizing:border-box;
	cursor:pointer;
	color:#2b5275;

	select {
		display:none;
		position:absolute;
		opacity:0;
		filter:alpha(opacity=0);
	}

	.select-head {
		display:inline-block;
		margin:10px 0 0 8px;
		font-size:16px;
		font-weight:bold;
		color:#2b5275;
	}

	.select-body {
		display:none;
		margin-top:5px;
		border:1px solid #b1b1b1;
		background:#fff;

		.list {
			height:30px;
			.value { display:inline-block; margin:8px 0 0 8px; }
			&:hover { background-color:#eee; }
			&.active { background-color:#39f; color:#fff;}
		}
	}

	&.active {
		.select-body {
			display:block;
			position:absolute;
			width:99%;
			z-index:99;
		}
	}

	/* position */
	&.right {
		position:absolute;
		top:0;
		right:0px;
	}
}



.search-field {
	&:after {content:""; display:block; clear:both;}
	padding:14px;
	border-bottom:1px solid $gray-0;

	> legend {
		text-indent:-9999px;
		position:absolute;
		width:0;
		height:0;
		overflow:hidden;
		visibility:hidden;
	}

	> label {
		float:left;
		width:83%;
	}

	.input-search {
		width:100%;
		border:0;
		margin:0;
		height:44px;
		padding:0 17px;
		background:$gray-0;
		box-sizing:border-box;
	}

	.btn_search {
		float:left;
		padding:0;
		margin:0;
		border:0;
		width:44px;
		height:44px;
		background:#999;
	}
}