footer {
	position:relative;
	min-height:150px;
	margin:0 auto;
	padding:60px 0;
	font-size:12px;
	color:#fff;
	background:$cobalt;
}


.footer-contents {
	max-width:$max-width;
	margin:0 auto;
	line-height:18px;
	&:after {content:''; display:block; clear:both;}

	.company {
		> a{
			color:#fff;
		}
		.footer-sns{
			margin-top:20px;
			> a {font-size:20px; color:#fff;}
		}
	}

	.footer-nav {
		h2 {color:#fff;}
	}

	.copyright {
		.copy {display:inline-block; margin-top:20px;}
		&, > a {color:#8195a7;}
	}
}