.section-filter {
	overflow:auto;
	position:fixed;
	top:0;
	left:0;
	bottom:0;
	width:300px;
	background:#fff;
	z-index:999;
}


.filter-field_price {
	@include filter-field;
	text-align:center;
	padding-left:30px;
	padding-right:30px;

	> .filter-tit {text-align:left;}
	> .btn-link {margin-top:20px;}
	> .slider-range {
		height:10px;
		margin-bottom:20px;
	}
	> .filter-tit {
		margin-left:-10px;
	}
}

.filter-field_color {
	@include filter-field;

	> .input-radio.filter > .label{
		padding:10px 20px;
	}
}

.filter-field_categories {
	@include filter-field;

	> .input-radio.filter > .label{
		padding:10px 20px;
	}
}

.filter-field_size {
	@include filter-field;

	> .input-radio.filter > .label{
		padding:10px 20px;
	}

	&.style-btn {
		padding-left:20px;
		padding-right:20px;
		> .input-checkbox.size {
			float:left;
			margin-top:-1px;
			margin-left:-1px;
		}

		.filter-tit {margin-left:0;}
	}
}