@mixin reset-a{
	&, a {
		color:#ababab;
		&:hover, &:focus, &:active {
			cursor:pointer;
			text-decoration:none;
		}
	}	
}

@mixin input-filter {
	display:block;
	color:#999;
	cursor:pointer;

	&.filter{
		.label {
			display:block;
			text-align:left;
			padding:10px 20px;
		}

		&.over {
			.label {background-color:#eee}
		}

		&.active {
			.label {
				color:#fff;
				background-color:#2b5275;
			}
		}
	}
}

@mixin info-setup {
	display:block;
	text-align:center;
}


@mixin filter-field {
	margin-top:-1px;
	border-top:1px solid #ebebeb;
	border-bottom:1px solid #ebebeb;
	padding:20px 0;

	legend {
		text-indent:-9999px;
		position:absolute;
		width:0;
		height:0;
		overflow:hidden;
		visibility:hidden;
	}

	&:first-child{
		margin-top:0;
		border-top:0;
	}

	.filter-tit {
		margin:0 0 20px 20px;
		font-weight:bold;
		font-size:14px;
		letter-spacing:-1px;
		color:#2b5275;
	}
}

@mixin contents-article {
	overflow:hidden;
	position:relative;
	margin:82px auto 0;
	&:first-child {margin-top:0;}
	&.news {padding:118px 0 130px;}
	&.related-items {padding: 78px 0 81px;}

	> .contents-title {
		padding-bottom:65px;
		letter-spacing:-1px;
		text-align:center;
		font-weight:bold;
		font-size:38px;
		color:$cobalt;

		&.left {
			max-width:$max-width;
			margin:0 auto;
			text-align:left;
			font-size:20px;
			padding-bottom:40px;
		}
	}
}