header {
	margin:0 auto;
}

.header-gnb {
	padding:5px 0;
	color:#fff;
	font-size:12px;
	background:$cobalt;

	a {
		color:#fff;
	}

	> .header-gnb_maxwidth {
		max-width:$max-width;
		margin:0 auto;

		> .header-event {
			float:left;
		}

		> .header-mymenu {
			float:right;

			> li {
				display:inline-block;
				margin-left:10px;
			}
		}

		&:after {content:""; display:block; clear:both;}
	}
}


.header-lnb {
	position:relative;
	max-width:$max-width;
	margin:0 auto;
	z-index:10;
	&:after{content:""; display:block; clear:both;}
	&.fixed {position:fixed;}

	a {
		color:#2b5275;
	}
	
	.header-logo {
		float:left;
		width:230px;
		margin-right:80px;
		line-height:80px;
		display:block;

		.header-img {
			vertical-align:middle;
		}
	}

	.header-menu_onedepth{
		float:left;
		line-height:80px;

		> .onedepth-list {
			position:relative;
			display:inline-block;
			padding:0 20px;

			&:first-child {
				margin-left:0;
			}

			&:hover {
				> .header-menu_twodepth {
					display:block;
				}
			}
		}

		.header-menu_twodepth {
			display:none;
			position:absolute;
			top:60px;
			left:0;
			width:120px;
			padding:30px;
			margin-left:-13px;
			border:1px solid $gray-0;
			background:$gray-menu;
			line-height:30px;

			> .twodepth-list > a{
				display:block;
			}
		}

		.back-img {
			width:320px;
			background-image:url("../images/menu_back.png");
			background-repeat:no-repeat;
			background-position:bottom right;
		}
	}

	.header-menu_etc {
		float:right;
		line-height:80px;
		&:after {content:''; display:display; }
		> li {
			float:left;
			margin-left:10px;
			> a {font-size:30px;}
		}
	}
}